.header {
  top: 0;
  left: 0;
  width: 100%;
  height: 5rem;
  background-color: #8a2b06;
  color: white;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 2.5%;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.25);
  z-index: 10;
  border-top-left-radius: 12px;
  border-top-right-radius: 12px;
}

.h1Sec {
  margin-left: 40px;
}

.rightSec {
  display: flex;
  gap: 1em;
}
